import React, { Component } from 'react';
import {
  CButton,
  CCard,
  CCardBody,
  CCol,
  CContainer,
  CInput,
  CSelect,
  CInputGroup,
  CModalBody,
  CModal,
  CModalHeader,
  CModalTitle,
  CModalFooter,
  CRow
} from '@coreui/react'
import axios from 'axios';
//import Box from '@material-ui/core/Box';
import { IconButton } from '@material-ui/core';
import PhotoCameraRoundedIcon from "@material-ui/icons/PhotoCameraRounded";
import {ToastsContainer, ToastsStore,ToastStore,ToastsContainerPosition} from "react-toasts"
import "../../scss/Wallet.scss";
import {Base_URL,headers} from "../../CommonRoutes/Api"
import BlockUI from 'src/CommonRoutes/BlockUI/BlockUI';

class AddOrder extends Component {
  constructor(props) {
    super(props);
    this.state = {
      statename:[],
      stateid:'',
      city: [],
      cityname:'',
      storesname: [],
      file:'',
      bill:"",
      store:[],
      invoice:'',
      store_id:'',
      subcategory:'',
      mobile:'',
      user_id:localStorage.getItem("user_id"),
      error:[],
      stateError:"",
      cityError:"",
      storeError:"",
      billError:"",
      invoiceError:"",
      fileError:"",
      image:'',
      blocking:false,
      modal:false,
      


    
      
      
    }
    this.handleCapture = this.handleCapture.bind(this)
    this.handleImage = this.handleImage.bind(this)
  }

  valid() {
    if(this.state.stateid == "" && this.state.cityname == "" && this.state.store_id == "" && this.state.bill == "" && this.state.file == "")
    {
        this.setState(
            {stateError:"Please select stores state",cityError:"Please select stores city",storeError:"Please select store name",billError :"Please enter the bill amount",fileError:"Please upload the bill copy"}
        )
    }

     else if(this.state.stateid == "")
     {
      this.setState(
        {stateError:"Please select stores state"}
    ) 
   /*   this.setState(  
           ToastsStore.warning("State is required")
             
             
              ) */
              return false
     } else

     if(this.state.cityname == "")
     {
         
      this.setState(  
           {cityError:"Please select stores city"}
             
             
              )
              return false
     } 
     if(this.state.store_id == "")
     {
       this.setState(
           {storeError:"Please select store name"}
       )  
           return false
     }    else 

     if(this.state.bill == "")
     {
       this.setState(
              {billError :"Please enter the bill amount"}
       )
       return false
     } else
    
    if(this.state.file == "")
    {
      this.setState(
           {fileError:"Please upload the bill copy"}
      )
      return false
    } else
    if(this.state.mobile == null)
    {
      this.setState(
           {modal:true}
      )
      return false
    } else
       return true
   }

  componentDidMount() {
   /*  if((!localStorage.getItem('token')) || (!localStorage.getItem('role')) || (!localStorage.getItem('user_id')))
    {

      this.props.history.push("/");
    } */
     this.setState({blocking:true})
    
    fetch(Base_URL + "allstates").then((response) => {
      response.json().then((result) => {
        console.log(result);
        this.setState({ statename: result.data,blocking:false})
      /*   setTimeout(
          () => this.setState({ position: 1,blocking:false}), 
          3000
        ); */
      })
    })
    this.setState({blocking:true})
    
    
    fetch(Base_URL + "getuserbyid/" +this.state.user_id).then((response) => {
      response.json().then((result) => {
          console.log(result);
          this.setState({profile:result,blocking:false})
         /*  setTimeout(
            () => this.setState({ position: 1,blocking:false}), 
            3000
            ); */
          console.log("mobile",this.state.profile[0].mobile);
          this.setState({mobile:this.state.profile[0].mobile})
         
      }) 
      
  })        
}

setClose(){
 window.location.href='/userupdateprofile'    
}

setModal = () => {
  this.setState({modal:false})
}


  changeState = (e) => { 
    this.setState({cityname : "",stateError:''});
     this.setState({ stateid: e.target.value,blocking:true});
    
    fetch( Base_URL + 'getCitybyStateid/' +e.target.value).then((resp) => {
      resp.json().then((result) => {
        this.setState({ city: result.data,blocking:false })
       /*  setTimeout(
          () => this.setState({ position: 1,blocking:false}), 
          3000
        ); */
      })
      
    })
  } 
changeCity = (e) => {
  
  this.setState({storesname : "",blocking:true});
   this.setState({ cityname: e.target.value});
   
 fetch(Base_URL + 'findvendor/' + e.target.value).then((resp) => {
    resp.json().then((result) => {
      
      this.setState({ storesname: result.data,blocking:false})
   })
 /*   setTimeout(
    () => this.setState({ position: 1,blocking:false }), 
    3000
  ); */
  })
} 


 handleCapture(e) { 
         console.log(e.target.files[0]);

         
        this.setState({
         file: URL.createObjectURL(e.target.files[0]),
         image: e.target.files[0]
           // file: e.target.files[0]
        })
     
       
    } 

    handleImage(e) {
   /*  if(this.state.file!=0){
          console.log("image",this.state.file)
         alert("bill is uploaded")
    } */
    
 } 
 /*  cancel(e) {
       this.setState({
         file:null
       })
  
}   */


    handleStoreName(e){
      console.log(e);
      console.log(this.state.storesname[e]);
      console.log("store",this.state.storesname[e]);
      console.log("store_id",this.state.storesname[e].user_id);
      console.log("subcategory",this.state.storesname[e].SubCat_id);
      this.setState({
           store_id:this.state.storesname[e].user_id,subcategory:this.state.storesname[e].SubCat_id
      })
    }

     submitForm(e){ 
        //console.log(this.state);
    this.setState(
        {stateError:"",cityError:"",storeError:"",billError:"",fileError:""}
    ) 
      if(this.valid()){ 
        let form_data = new FormData();
        form_data.append('invoiceImage', this.state.image);
        form_data.append("city",this.state.cityname);
        form_data.append("vendor_id",this.state.store_id);
        form_data.append("subcat_id",this.state.subcategory);
        form_data.append("invoiceAmount",this.state.bill);
         form_data.append("invoice_no",this.state.invoice);
      form_data.append("user_id",localStorage.getItem("user_id"));
       form_data.append("usermobile",this.state.mobile);
        let url = Base_URL + 'addinvoice';
        axios.post(url, form_data, {
            headers
        })
        .then(function (response) {
          ToastsStore.error(response.data.message)
          //handle success
        
          console.log(response);
        })
        .catch(error => {
          
          //handle error
          console.log(error);
          this.setState({error:error})
        });
   
    }
    else{
      this.setState({modal:true})
    }
  }

  render() {
    let inputStyle = {};
    
    if(this.state.stateid.length > 0) {
      inputStyle = {
           backgroundColor:"rgb(232, 240, 254)",borderRadius: '30px',border: '1px solid #3B5998',
      } 
    } else{
      inputStyle = {
        borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998'
   } 
    } 
    let inputStyle2 = {};


    if(this.state.cityname.length > 0) {
      inputStyle2 = {
           backgroundColor:"rgb(232, 240, 254)",borderRadius: '30px',border: '1px solid #3B5998',
      } 
    } else{
      inputStyle2 = {
        borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998'
   } 
    } 

   
    console.log(this.state.error);
    
    return (
      <>
      <CModal 
              show={this.state.modal} 
              //onClose={setModal}
            >
              <CModalHeader closeButton>
              </CModalHeader>
              <CModalBody>
                Please update your mobile number
              </CModalBody>
              <CModalFooter>
                <CButton color="primary" onClick={this.setClose}>ok</CButton>
                <CButton 
                  color="secondary" 
                  onClick={this.setModal}
                >Cancel</CButton>
              </CModalFooter>
            </CModal>
       <ToastsContainer position={ToastsContainerPosition.TOP_RIGHT} store={ToastsStore}/>
        <CContainer style={{ padding: "10px",marginTop:"-1.6rem" }}>
          <CRow className="justify-content-center">
            <CCol md="12" lg="12" xl="12" style={{ padding: "0px" }}>
            
              <CCard className="mx-10"  style={{ backgroundColor: '#110730', marginLeft: '-1rem', marginRight: '-.6rem', marginBottom: '-.1rem' }}>
              <center><h4 style={{color:"orange",marginTop:"0.3rem"}}>-Add Order-</h4></center>
                <CCardBody className="p-10">
                  {/*  <CForm > */} 
                  <label style={{color:"white"}}>State</label>
                  <CInputGroup className="mb-3">
                    <CSelect value=""
                      id="select" selected
                     value={this.state.stateid}
                    required  onChange={this.changeState}
                      style={inputStyle}
                      name="statename">
                         <option>Select State</option> 
                      {
                        this.state.statename && this.state.statename.length > 0 ?
                        this.state.statename.map((item) =>
                             <option item={item} value={item.id}>{item.state_name}</option>

                        ):'No State Found'}
                    </CSelect>
                  </CInputGroup>
                  <div> {this.state.stateError ? (
                    <p style={{ color: "red",marginTop:"-0.6rem"}}> {this.state.stateError}</p>) : null}  </div>
                    
                  <label style={{color:"white"}}>Stores City</label>
                    <CInputGroup className="mb-3">
                      <CSelect
                        id="select" selected
                        value={this.state.cityname}
                        name="city"
                        onChange={this.changeCity}
                        style={inputStyle2}
                       // style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                        >
                        <option>select city</option>
                        {
                        this.state.city && this.state.city.length > 0 ?
                         this.state.city.map((item) =>
                           <option item={item} value={item.city_name}>{item.city_name}</option>
                          ) : 'No City Found'}
                      </CSelect>
                    </CInputGroup>
                    <div> {this.state.cityError ? (
                    <p style={{ color: "red",marginTop:"-0.6rem" }}> {this.state.cityError}</p>) : null}  </div>

                    <label style={{color:"white"}}>Stores Name</label>
                    <CInputGroup className="mb-3">
                      <CSelect
                        placeholder="Select Stores"
                        name="vendorID"
                        id="select"
                        onChange={(event) => { this.handleStoreName(event.target.value) }}
                        style={inputStyle2}
                      >
                        <option>select stores</option>
                          {
                        this.state.storesname && this.state.storesname.length > 0 ?
                          this.state.storesname.map((item, i) =>
                            <option  item={item} value={i} >{item.CompanyName}</option>

                          ) : 'No Stores Available'} 
                      </CSelect>
                    </CInputGroup>
                    <div> {this.state.storeError ? (
                    <p style={{ color: "red",marginTop:"-0.6rem" }}> {this.state.storeError}</p>) : null}  </div>

                    <label style={{color:"white"}}>Bill Amount</label>
                    <CInputGroup className="mb-3">
                      <CInput type="number"
                        name="invoiceAmount"
                        placeholder="Enter the bill amount"
                        autoComplete="false"
                        value={this.state.bill}
                       onChange={(event) => (this.setState({ bill: event.target.value }))}
                       style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                      />
                     
                    </CInputGroup>
                    <div> {this.state.billError ? (
                    <p style={{ color: "red",marginTop:"-0.6rem" }}> {this.state.billError}</p>) : null}  </div>
                    <label style={{color:"white"}}>Invoice Number</label>
                    <CInputGroup className="mb-3">
                      <CInput type="text"
                        name="invoice"
                        placeholder="Enter the Invoice Number"
                        autoComplete="false"
                        value={this.state.invoice}
                       onChange={(event) => (this.setState({ invoice: event.target.value }))}
                       style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                      />
                     
                    </CInputGroup>
                    <div> {this.state.invoiceError ? (
                    <p style={{ color: "red",marginTop:"-0.6rem" }}> {this.state.invoiceError}</p>) : null}  </div>
                    <center> <span style={{ fontSize: "1rem", color: "white" }}>Attach Bill Copy</span> </center>
                    <CCol xs="12" md="12" lg="12" className="content-center">

                    <input
                    accept="image/*"
                  // className={classes.input}
                    id="icon-button-file"
                    type="file"
                    name="invoiceImage"
                    style={{display:"none"}}
                    capture="environment"
                    onChange={this.handleCapture}
                    
          />       
                   <center> <label htmlFor="icon-button-file">
                   <div> {this.state.file!=0?(

                   <IconButton
                     color="primary"
                      aria-label="upload picture"
                       component="span"
                       onClick={(event) => (this.setState({ file: "" }))}
                       >
                   
            </IconButton>):null
                   }</div> 

                  
                      <IconButton
                     color="primary"
                      aria-label="upload picture"
                       component="span"
                       >
                      <PhotoCameraRoundedIcon fontSize="large" color="primary" /> 
            </IconButton>
          
          </label></center> 
         
               <br></br>
               

                    </CCol>
                  <center>  {this.state.file &&
            <card display="flex" justifyContent="center" border={1} style={{ maxWidth: "30%",
    maxHeight: "80%",
    border:"2px blue",
    margin: "10px"}}>
              
              <img src={this.state.file} alt={"snap"} style={{ height: "inherit",
    maxWidth: "inherit"}}></img>
            </card>}   </center>
            <div> {this.state.fileError ? (
                    <p style={{ color: "red",marginTop:"-0.6rem",textAlign:"center" }}> {this.state.fileError}</p>) : null}  </div>

                    <CCol xs="12" md="12" lg="12" className="content-center" style={{ paddingLeft: "1rem" }}>
                      <CButton input type="reset" color="primary" className="addWalletbtn" style={{
                        width: "8rem",
                        height: "2rem",
                        borderBottomLeftRadius: "1rem",
                        borderTopLeftRadius: "1rem",
                        marginLeft: "-1.3rem"
                      }}> Cancel</CButton>
                      <CButton color="warning" className="addWalletbtn" style={{
                        width: "8rem",
                        height: "2rem",
                        borderBottomRightRadius: "1rem",
                        borderTopRightRadius: "1rem",
                        marginLeft: "0.3rem"}}
                        onClick={(e)=>this.submitForm()}
                      > Submit</CButton>

                    </CCol>
                 {/*  </CForm>  */}
                </CCardBody>
              </CCard>
            </CCol>
          </CRow>
        </CContainer>
        <BlockUI blocking={this.state.blocking} />
      </>
    );
  }
}

export default AddOrder;